body {
  color: #fff;
  font-family: Roboto, Helvetica, sans-serif;
}

.card {
  background-color: #f72648;
  background-image: linear-gradient(#000, #000);
  border-style: solid;
  border-color: #fff;
  border-radius: 15px;
}

.card__title {
  color: #fff;
}

.card_footer {
  background-color: #fff;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

.button {
  color: #fff;
  background-color: #000;
  border-style: solid;
  border-color: #fff;
  border-radius: 15px;
}

.button:hover {
  color: #000;
  background-color: #fff;
  border-style: solid;
  border-color: #000;
}

